.cta {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__title {
    font-size: 3rem;
    margin-bottom: 30px;
  }

  &__description {
    font-size: 1.125rem;
    line-height: 130%;
    margin-bottom: 50px;
  }

  &__form {
    margin-bottom: 20px;
    display: flex;
    flex-wrap: wrap;

    label {
      width: 100%;
      height: 50px;
      margin-bottom: 20px;
    }

    input {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      border: 3px solid $accent;
      border-radius: 5px;
      padding: 0 12px;
      font-size: 1rem;
      outline: none;

      &::placeholder {
        font-size: inherit;
      }

      &:focus {
        box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.5);
      }
    }
  }

  &__list {
    display: flex;
    list-style: none;

    &-item {
      display: flex;
      align-items: center;
      font-size: 0.75rem;
      line-height: 115%;

      &::after {
        content: '';
        display: block;
        width: 5px;
        height: 5px;
        background: $text;
        border-radius: 50%;
        margin: 0 10px;
      }

      &:last-child::after {
        display: none;
      }
    }
  }

  &__image {
    display: none;
  }
}

@media(min-width: $brp-xs) {
  .cta {

    &__list {

      &-item {
        font-size: 0.875rem;
      }
    }

    &__form {
      flex-wrap: nowrap;

      label {
        flex: 0 1 auto;
        max-width: 250px;
        margin-bottom: 0;
      }

      input {
        border-radius: 5px 0 0 5px;
      }
    }
  }
}

@media(min-width: $brp-sm) {
  .cta {

    &__form {

      label {
        width: 100%;
      }

      input {
        border-radius: 5px 0 0 5px;
      }
    }
  }
}

@media(min-width: $brp-md) {
  .cta {

    &__form {

      label {
        width: 100%;
      }

      input {
        border-radius: 5px 0 0 5px;
      }
    }
  }
}

@media(min-width: $brp-lg) {
  .cta {
    
    &__content {
      flex: 1 1 auto;
    }

    &__title {
      max-width: 700px;
      font-size: 3.5rem;
    }

    &__description {
      max-width: 580px;
    }

    &__form {

      label {
        width: 100%;
      }

      input {
        border-radius: 5px 0 0 5px;
      }
    }

    &__image {
      display: block;
      width: 100%;

      img {
        display: block;
        width: 100%;
        object-fit: contain;
      }
    }
  }
}


